import axios from 'axios';
const CancelToken = axios.CancelToken;

const apiTag = '/api/';
const actualPath = 'https://stackxchange.hopto.org'

const apiConfig = {
  getUser: function(params) {
    return {
      api: axios.get(`${apiTag}user`),
      cancelToken: CancelToken.source()
    };
  },

  login: function(params) {
    return {
      api: axios.get(`${actualPath}/auth/login/stackoverflow`),
      cancelToken: CancelToken.source()
    };
  },

  getBounties: function(params) {
    return {
      api: axios.get(`${apiTag}bounties`, params),
      cancelToken:  CancelToken.source()
    };
  },

  getEthPrice: function() {
    return axios.get('https://api.coinmarketcap.com/v2/ticker/1027/')
  }
}

export default apiConfig;


