function isAuthenticated() {
  return (localStorage.getItem('userItem') && localStorage.getItem('userItem').name)
}

function getUser() {
  let user;
  try {
    user = JSON.parse(localStorage.getItem('userItem'))
  } catch(e) {
    user = null;
  }
  return user;
}

function setUser(user) {
  localStorage.setItem('userItem', JSON.stringify(user));
}

function logout() {
  localStorage.removeItem('userItem');
}

export default {
  isAuthenticated,
  getUser,
  setUser,
  logout
}


