import ActionTypes from './actionTypes';

export function updateUser(user) {
  return {
    type: ActionTypes.UPDATE_USER,
    payload: user
  }
}

export function updateEthPrice(price) {
  return {
    type: ActionTypes.UPDATE_ETH_PRICE,
    payload: price
  }
}


