import React, { Component } from 'react';

export default class PostBounty extends Component {
  constructor(props) {
    super(props);
    this.state = {
      url: null,
      bounty: null,
      time: 30
    }
    this.handleUrlChange = this.handleUrlChange.bind(this);
  }

  handleUrlChange(value) {
    console.log(value);
    this.setState({
      url: value
    })
  }

  render() {
    const { url, bounty, time } = this.state;
    return (
      <div>
        <div className='layout horizontal center center-justified heading m-b'>Post Bounty</div>
        <div className='layout vertical flex-6 p form-container'>
          <div className='layout horizontal justified m-b center'>
            <div className='text-grey font-sm'>URL</div>
            <input onChange={this.handleUrlChange} value={url} />
          </div>
          <div className='layout horizontal justified m-b center'>
            <div className='text-grey font-sm'>Bounty</div>
            <input onChange={this.handleUrlChange} value={url} />
          </div>
          <div className='layout horizontal justified m-b center'>
            <div className='text-grey font-sm'>Time limit</div>
            <input onChange={this.handleUrlChange} value={url} />
          </div>
          <div className='btn self-center text-black'>Submit</div>
        </div>

      </div>
    )
  }
}



