import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';

import { updateEthPrice } from '../../redux/actions';
import Api from '../../api';
import Navigation from '../navigation';
import Explorer from '../explorer';

class Home extends Component {
  componentDidMount() {
    this.registerEthPricePolling();
  }

  registerEthPricePolling() {
    setTimeout(() => {
      this.api = Api.getEthPrice();
      this.api.then(res => {
        let price;
        try {
          price = res.data.data.quotes.USD.price;
          this.props.updateEthPrice(price);
        } catch(e) {

        }
        this.registerEthPricePolling();
      }, err => {
        this.registerEthPricePolling();
      })
    }, 6000);
  }

  render() {
    return (
      <div className='h-100 no-scroll'>
        <Navigation />
        <Explorer />
      </div>
    )
  }
}

export default connect(
  () => {},
  (dispatch) => bindActionCreators({ updateEthPrice }, dispatch)
)(Home);



