import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import Modal from 'react-responsive-modal';

import Profile from './profile';
import PostBounty from './post-bounty';
import AuthService from '../../services/auth';
import Api from '../../api';

import './navigation.css';

class Navigation extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showPost: false
    }
    this.handlePostBountyClick = this.handlePostBountyClick.bind(this);
    this.onCloseModal = this.onCloseModal.bind(this);
  }

  handlePostBountyClick() {
    // if (!AuthService.isAuthenticated()) {
    //   Api.login();
    //   return;
    // }
    this.setState({
      showPost: true
    })
  }

  onCloseModal() {
    this.setState({
      showPost: false
    })
  }

  render() {
    const { showPost } = this.state;
    return (
      <div className='layout horizontal center bg-prime banner'>
        <div className='color-prime head text-white'>
          <Link to='/'><div>Stack-X-change</div></Link>
        </div>
        <div className='layout horizontal end'>
          <div className='btn m-r' onClick={this.handlePostBountyClick}>
            Post Bounty
          </div>
          <Modal open={showPost} onClose={this.onCloseModal} center classNames={{ modal: 'modal-stack'}}>
            <PostBounty />
          </Modal>
          <Profile />
        </div>
      </div>
    );
  }
}

Navigation.propTypes = {
}

export default Navigation;



