import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux';

import './bounty-list.css';
class BountyList extends Component {
  constructor(props) {
    super(props);
    this.renderEntity = this.renderEntity.bind(this);
  }

  renderEntity(entity, index) {
    const { price } = this.props;
    return (
      <div className='layout horizontal card m-b-lg no-wrap'>
        <div>
          <div className='bounty-amt'>
            <div className='amt eth'>
              <div className='low'>Eth</div>
              <div>{entity.bounty}</div>
            </div>
            <div className='amt dollar'>
              <div className='low'>USD</div>
              { price && <div>{entity.bounty * price}</div> }
              { !price && <div>-</div> }
            </div>
          </div>
        </div>
        <div className='flex-1 layout vertical'>
          <div className='source'>{entity.source}</div>
          <div className='p flex-auto'>
            <div className='bounty-title'>{entity.title}</div>
            <div className='tags'>
              {
                entity.tags.map(tag => <span className='tag'>{tag}</span>)
              }
            </div>
          </div>
          <div className='claim'>
            Claim bounty
          </div>
        </div>
        <div>
          <div className='view-btn'>View</div>
        </div>
      </div>
    )
  }

  render() {
    const { bountyList, loading} = this.props;

    if (loading) {
      return <div>Loading...</div>
    }

    return (
      <div className='p'>
        {
          bountyList.map(this.renderEntity)
        }
      </div>
    )
  }
}

BountyList.propTypes = {
  loading: PropTypes.bool,
  bountyList: PropTypes.array,
  price: PropTypes.object
}

export default connect(
  (state) => state.price.toJS().price,
  dispatch => ({})
)(BountyList)



