import React, { Component } from 'react'
import PropTypes from 'prop-types'

import Api from '../../api';
import stockBounties from './staticBounty';
import BountyList from './bounty-list';

import './explorer.css';

export default class Explorer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      bountyList: stockBounties,
      err: null
    }
  }

  componentDidMount() {
    // this.loadItems(this.props);
  }

  loadItems(params) {
    const { router: { location: { queryParams }}} = params;
    console.log(params);
    console.log(queryParams);
    this.setState({
      loading: true
    })

    // todo: fix
    const apiParams = {};
    this.query = Api.getBounties(apiParams);

    this.query.then(res => {
      const bountyList = res.body;
      this.setState({
        bountyList,
        err: null
      })
    }, err => {
      this.setState({
        bountyList: [],
        err: err
      })
    });
  }

  render() {
    const { loading, bountyList, err } = this.state;

    if (err) {
      return <div>Some error, please refresh</div>
    }

    return (
      <div className='view'>
        <div className='side-panel layout vertical'>
        </div>
        <div className='main-panel'>
          <BountyList loading={loading} bountyList={bountyList} />
        </div>
      </div>
    )
  }
}

Explorer.propTypes = {
  router: PropTypes.object
}



